import { ViewChild, Input, ViewContainerRef, SimpleChanges, ElementRef, QueryList } from '@angular/core';
import { Editor, Block, Node } from 'slate';

export class SlaPluginComponentBase {
    @Input()
    editor: Editor;

    @Input()
    node: Block;

    @Input()
    parent: Node;

    @Input()
    isFocused: boolean;

    @Input()
    isSelected: boolean;

    @Input()
    readOnly: boolean;

    @Input()
    children: QueryList<any>;

    @Input()
    attributes: { [key: string]: string };

    @Input()
    nodeViewContainerRef: ViewContainerRef;

    @ViewChild('childrenContent', { read: ElementRef, static: true })
    childrenContent: ElementRef;

    constructor(public elementRef: ElementRef) {
    }

    initPluginComponent() {
        this.insertChildrenView();
        this.setNodeAttributes(this.elementRef.nativeElement, this.attributes);
    }

    getData(key) {
        return this.node.data.get(key);
    }

    isNodeChange(changes: SimpleChanges): boolean {
        const node = changes.node;
        if (node && !node.isFirstChange) {
            return true;
        }
        return false;
    }

    updateHostClass(classMap: { [key: string]: boolean }) {
        for (const key in classMap) {
            if (classMap.hasOwnProperty(key)) {
                const value = classMap[key];
                const classList = (this.elementRef.nativeElement as HTMLElement).classList;
                if (value) {
                    if (!classList.contains(key)) {
                        classList.add(key);
                    }
                } else {
                    if (classList.contains(key)) {
                        classList.remove(key);
                    }
                }
            }
        }
    }

    setNodeAttributes(ele: HTMLElement, attributes: { [key: string]: string }) {
        for (const key in attributes) {
            if (attributes.hasOwnProperty(key)) {
                ele.setAttribute(key, attributes[key]);
            }
        }
    }

    private insertChildrenView() {
        if (this.childrenContent) {
            const nodeRefs = this.children.toArray();
            for (const nodeRef of nodeRefs) {
                this.childrenContent.nativeElement.appendChild(nodeRef.rootNode);
            }
        }
    }

    removeNode(event) {
        event.preventDefault();
        const path = this.editor.value.document.getPath(this.node.key);
        let focusNode = this.editor.value.document.getPreviousBlock(path);
        if (!focusNode) {
            focusNode = this.editor.value.document.getNextBlock(path);
        }
        this.editor.focus().moveToEndOfNode(focusNode);
        this.editor.removeNodeByKey(this.node.key);
    }
}
