import { Injectable, ComponentFactoryResolver, EmbeddedViewRef, ComponentRef, QueryList } from '@angular/core';
import { SlaNodeRenderConfig, SlaNestedNodeRef } from './render-config';
import { ComponentType } from '@angular/cdk/portal';

@Injectable({
    providedIn: 'root'
})
export class SlaPluginRenderService {
    constructor(private cfr: ComponentFactoryResolver) {}
    public renderDom(
        tagName: string,
        children: any,
        attributes: { [key: string]: string },
        styles?: { [key: string]: string }
    ): HTMLElement {
        const node = document.createElement(tagName);
        this.setNodeAttributes(node, attributes);
        if (styles) {
            this.setNodeStyles(node, styles);
        }

        if (children instanceof HTMLCollection) {
            for (let index = 0; index < children.length; index++) {
                const element = children.item(index);
                node.appendChild(element);
            }
            return node;
        }
        if (children instanceof HTMLElement) {
            node.appendChild(children);
            return node;
        }
        if (children instanceof QueryList) {
            const nodeRefs = children.toArray();
            for (const nodeRef of nodeRefs) {
                node.appendChild(nodeRef.rootNode);
            }
            return node;
        }
        return node;
    }

    public renderComponent(componentType: ComponentType<any>, config: SlaNodeRenderConfig): SlaNestedNodeRef {
        const componentFactory = this.cfr.resolveComponentFactory(componentType);
        const componentRef = config.nodeViewContainerRef.createComponent(componentFactory);
        Object.assign(componentRef.instance, { ...config });
        componentRef.changeDetectorRef.detectChanges();
        return new SlaNestedNodeRef(this.getComponentRootNode(componentRef), componentRef);
    }

    private setNodeAttributes(ele: HTMLElement, attributes: { [key: string]: string }) {
        for (const key in attributes) {
            if (attributes.hasOwnProperty(key)) {
                ele.setAttribute(key, attributes[key]);
            }
        }
    }

    private setNodeStyles(ele: HTMLElement, styles: { [key: string]: string }) {
        for (const key in styles) {
            if (styles.hasOwnProperty(key)) {
                ele.style[key] = styles[key];
            }
        }
    }

    private getComponentRootNode(componentRef: ComponentRef<any>): HTMLElement {
        return (componentRef.hostView as EmbeddedViewRef<any>).rootNodes[0] as HTMLElement;
    }
}
