import { Injectable } from '@angular/core';
import { NGX_SLATE_EVENTS, NgxSlateEvent } from '../constants/event-handlers';
import { fromEvent, Subject, merge, Observable } from 'rxjs';
import { takeUntil, map } from 'rxjs/operators';

@Injectable({
    providedIn: 'root'
})
export class SlaEventService {
    fromSlaEvents(element: HTMLElement, $destroy: Subject<any>): Observable<{ event: Event; eventEntity: NgxSlateEvent }> {
        return merge(
            ...NGX_SLATE_EVENTS.map((eventEntity: NgxSlateEvent) => {
                return fromEvent(element, eventEntity.name).pipe(
                    map(event => {
                        return { event, eventEntity };
                    })
                );
            })
        ).pipe(takeUntil($destroy));
    }
}
