import {
    Component,
    Input,
    OnInit,
    ElementRef,
    ViewContainerRef,
    EmbeddedViewRef,
    QueryList,
    HostBinding
} from '@angular/core';
import {
    Document,
    Selection,
    PathUtils,
    Node,
    Editor,
    Block,
    Inline,
    Decoration,
    Annotation,
    Text
} from 'slate';
import { List, Map } from 'immutable';
import {
    SlaNestedNodeRef,
} from '../../core/render-plugin/render-config';
import DATA_ATTRS from '../../constants/data-attributes';
import { setNodeStyles, setNodeAttributes } from '../../utils/attributes';


@Component({
    selector: 'div[slaVoid]',
    templateUrl: './void.component.html',
})
export class SlaVoidComponent implements OnInit {

    internalNode: any;

    nodeComponentRef: SlaNestedNodeRef;

    @Input()
    editor: Editor;

    @Input()
    selection: Selection;

    @Input()
    parent: Document | Block | Inline;

    @Input()
    block: Block;

    @Input()
    decorations: List<Decoration>;

    @Input()
    annotations: Map<string, Annotation>;

    @Input()
    children: HTMLElement;

    @Input()
    nodeRef: (nodeRef: any) => {};

    @Input()
    readOnly = false;

    @Input()
    nodeRefs: QueryList<any>;

    @Input()
    set node(value: any) {
        this.internalNode = value;
        if (this.nodeComponentRef) {
            this.nodeComponentRef.componentRef.instance.node = this.internalNode;
        }
    }

    get node() {
        return this.internalNode;
    }

    @HostBinding('attr.data-slate-void')
    void = 'true';

    @HostBinding('attr.data-key')
    key = '';

    constructor(
        private viewContainerRef: ViewContainerRef,
        private elementRef: ElementRef<any>
    ) { }

    ngOnInit() {
        this.render();
        this.key = this.node.key;
        if (this.node.object === 'inline') {
            this.elementRef.nativeElement.contentEditable = 'false';
        }
    }

    createElement() {
        const tag = this.node.object === 'block' ? 'div' : 'span';
        return document.createElement(tag);
    }

    render() {
        if (!this.readOnly) {
            this.elementRef.nativeElement.appendChild(this.renderSpacer());
        } else {
            this.children.remove();
        }
        this.elementRef.nativeElement.appendChild(this.renderContent());
    }

    renderSpacer() {
        const style = {
            height: '0',
            color: 'transparent',
            outline: 'none',
            position: 'absolute',
        };
        const spacerAttrs = {
            [DATA_ATTRS.SPACER]: 'true',
        };
        const spacer = this.createElement();
        setNodeStyles(spacer, style);
        setNodeAttributes(spacer, spacerAttrs);
        spacer.appendChild(this.nodeRefs.first.rootNode);
        return spacer;
    }

    renderContent() {
        const content = this.createElement();
        content.setAttribute(`contentEditable`, this.readOnly ? null : 'false');
        content.appendChild(this.children);
        return content;
    }

}
