import EVENT_HANDLERS from '../../constants/event-handlers';

const PROPS = [
    ...EVENT_HANDLERS,
    'commands',
    'decorateNode',
    'queries',
    'renderAnnotation',
    'renderBlock',
    'renderDecoration',
    'renderDocument',
    'renderEditor',
    'renderInline',
    'renderMark',
    'schema'
];

function EditorPropsPlugin(options = {}) {
    const plugin = PROPS.reduce((memo, prop) => {
        if (prop in options) {
            if (options[prop]) {
                memo[prop] = options[prop];
            }
        }
        return memo;
    }, {});

    return plugin;
}

export default EditorPropsPlugin;
