import {
    Component,
    OnInit,
    ViewEncapsulation,
    Renderer2,
    ElementRef,
    NgZone,
    Input,
    Output,
    EventEmitter,
    ViewChild
} from '@angular/core';
import { Editor, Plugin } from 'slate';
import AngularPlugin from '../../plugins/angular/index';
import { SlaContentComponent } from '../content/content.component';
import { ValueChange } from '../../core/value-change';
import { Rendering } from '../../plugins/angular/rendering';

@Component({
    selector: 'sla-editor,[slaEditor]',
    templateUrl: './editor.component.html'
})
export class SlaEditorComponent implements OnInit {
    editor: Editor;

    @Input()
    slaValue: any;

    @Input()
    slaReadOnly: boolean;

    @Input()
    slaPlaceholder: string;

    @Input()
    spellcheck = false;

    @Input()
    tabIndex: number;

    @Input()
    slaContainerClass;

    @Input()
    plugins: Plugin[] = [];

    @Input()
    commands: any;

    @Input()
    queries: any;

    @Input()
    schema: any;

    @Input()
    decorateNode: () => {};

    @Input()
    renderAnnotation: () => {};

    @Input()
    renderBlock: () => {};

    @Input()
    renderDecoration: () => {};

    @Input()
    renderDocument: () => {};

    @Input()
    renderEditor: () => {};

    @Input()
    renderInline: () => {};

    @Input()
    renderMark: () => {};

    @Input()
    onBeforeInput: () => {};

    @Input()
    onBlur: () => {};

    @Input()
    onClick: () => {};

    @Input()
    onContextMenu: () => {};

    @Input()
    onCompositionEnd: () => {};

    @Input()
    onCompositionStart: () => {};

    @Input()
    onCopy: () => {};

    @Input()
    onCut: () => {};

    @Input()
    onDragEnd: () => {};

    @Input()
    onDragEnter: () => {};

    @Input()
    onDragLeave: () => {};

    @Input()
    onDragOver: () => {};

    @Input()
    onDragStart: () => {};

    @Input()
    onDrop: () => {};

    @Input()
    onInput: () => {};

    @Input()
    onFocus: () => {};

    @Input()
    onKeyDown: () => {};

    @Input()
    onKeyUp: () => {};

    @Input()
    onMouseDown: () => {};

    @Input()
    onMouseUp: () => {};

    @Input()
    onPaste: () => {};

    @Input()
    onSelect: () => {};

    @Output()
    slaOnChange: EventEmitter<ValueChange> = new EventEmitter<ValueChange>();

    @Output()
    slaEditorInitComplete: EventEmitter<Editor> = new EventEmitter<Editor>();

    @ViewChild(SlaContentComponent, { static: true })
    contentRef: SlaContentComponent;

    tmp = {
        mounted: false,
        change: null,
        resolves: 0,
        updates: 0,
        contentRef: null
    };

    constructor(
        private ngZone: NgZone,
        private render2: Renderer2,
        private element: ElementRef,
        private rendering: Rendering
    ) {}

    ngOnInit() {
        this.setEditorContainerClass();
        const { slaValue: value, slaReadOnly: readOnly } = this;
        this.ngZone.runOutsideAngular(() => {
            const angularPlugins = AngularPlugin(this);

            const onChange = (change: ValueChange) => {
                // if (this.tmp.mounted) {
                //     this.slaOnChange.emit(change);
                // } else {
                //     this.tmp.change = change;
                // }
                this.slaOnChange.emit(change);
            };

            this.editor = new Editor({
                plugins: [...angularPlugins, this.rendering],
                onChange,
                value,
                readOnly
            });
            (this.editor as any).tmp.contentRef = this.contentRef;
            this.slaEditorInitComplete.emit(this.editor);
        });
    }

    slaEvent(handler: string, event: Event) {
        this.editor.run(handler, event);
    }

    private setEditorContainerClass() {
        const classList = ['sla-editor-container'];
        if (this.slaContainerClass) {
            classList.push(this.slaContainerClass);
        }
        this.element.nativeElement.classList.add(...classList);
    }
}
